import { createContext, FC, useEffect, useMemo, useState } from "react";
import { ReactNode } from "react";
import { INITIAL_PAGE, PAGE_SIZE } from "../../constants";
import { useUsers } from "../../hooks/useUsers";
import { User } from "../../types/type";

export interface UserContextProviderProps {
  children: ReactNode;
}

export interface UserContextParams {
  users?: User[];
  totalPages: number | null;
  reset?: () => void;
  pageState?: [number, React.Dispatch<React.SetStateAction<number>>];
  onShowMore: () => void;
  sortedUsers: User[];
  page: number;
  loading: boolean;
}

const initialContext: UserContextParams = {
  users: [],
  totalPages: null,
  onShowMore: function (): void {
    throw new Error("Function not implemented.");
  },
  sortedUsers: [],
  page: 0,
  loading: false,
};

export const UserContext = createContext(initialContext);

export const UserContextProvider: FC<UserContextProviderProps> = ({
  children,
}) => {
  const [users, setUsers] = useState<User[]>([]);
  const [totalPages, setTotalPages] = useState<null | number>(null);

  const [page, setPage] = useState(INITIAL_PAGE);

  const usersState = useUsers({ pageSize: PAGE_SIZE, page: page });

  const { value, loading } = usersState;

  useEffect(() => {
    if (value?.total_pages) {
      setTotalPages(value?.total_pages);
    }
  }, [value?.total_pages]);

  const isCanMoveNextPage = () => {
    if (!totalPages) return false;
    const isCan = page < totalPages;

    return isCan;
  };

  const next = () => {
    const isCanMove = isCanMoveNextPage();

    if (isCanMove) {
      setPage((prev) => prev + 1);
      return;
    }
    return;
  };

  useEffect(() => {
    const isInitialPage = page === INITIAL_PAGE;

    if (isInitialPage) {
      setUsers(value?.users || []);
      next();

      return;
    }

    if (value?.users) {
      setUsers((prev) => [...prev, ...value.users]);
    }
    // eslint-disable-next-line
  }, [value?.users]);

  const sortedUsers = useMemo(() => {
    return users.sort(
      (a, b) => b.registration_timestamp - a.registration_timestamp
    );
  }, [users]);

  const reset = () => {
    setPage(INITIAL_PAGE);
  };

  const onShowMore = () => {
    next();
  };

  return (
    <UserContext.Provider
      value={{ sortedUsers, totalPages, onShowMore, page, reset, loading }}
    >
      {children}
    </UserContext.Provider>
  );
};
